<template>
    <div class="seriesList">
        <Button v-if="isManager" @click.native="newFlag = true">新建分组</Button>
        <Button  v-if="isManager" type="primary" style="margin-left:10px;" @click.native="newItem">新建项目</Button>
        <div class="wrap" v-for="(item, index) in groupList" :key="item.objectId">
            <div class="title">
            {{item.title}}
            <div class="action" v-if="isManager" @click="deleteGroup(index)">删除分组</div>
            </div>
            <div class="card-wrap">
                <Card
                :class="{'first': eleIndex % 5 === 0}"
                v-for="(ele, eleIndex) in item.itemList"
                :key="eleIndex"
                @click.native="editItem(index, eleIndex)"
                >
                <p slot="title" style="margin-bottom:0px;vertical-align: middle;">{{ele.title}}</p>
                <div slot="extra" style="cursor: pointer;" v-if="isManager">
                    <Icon type="ios-trash-outline" @click.native.stop="deleteItem(index, eleIndex)"/>
                    <!-- <Dropdown>
                        <a href="javascript:void(0)">
                            <Icon type="md-more"/>
                        </a>
                        <DropdownMenu slot="list">
                            <DropdownItem @click.native="copy(index, eleIndex)">复制</DropdownItem>
                            <Dropdown placement="right-start" v-if="groupList.length > 1">
                                <DropdownItem>
                                    移动到
                                    <Icon type="ios-arrow-forward"></Icon>
                                </DropdownItem>
                                <DropdownMenu slot="list">
                                    <DropdownItem
                                    v-for="(groupItem, groupIndex) in groupList"
                                    :key="groupIndex"
                                    v-show="index !== groupIndex"
                                    @click.native="remove(index, eleIndex, groupIndex)"
                                    >{{groupItem.title}}</DropdownItem>
                                </DropdownMenu>
                            </Dropdown>
                            <DropdownItem @click.native="deleteItem(index, eleIndex)">删除</DropdownItem>
                        </DropdownMenu>
                    </Dropdown> -->
                </div>
                <p>{{ele.info}}</p>
                </Card>
            </div>
        </div>
        <div v-if="false">
        <div v-for="(item, index) in groupList" :key="index">
            {{item.title}}
        </div>
        </div>
        <Modal v-model="deleteGroupFlag" title="删除分组" @on-ok="confirmDeleteGroup">
            <p>删除该分组会也会删除该分组下面的算法，确认删除？</p>
        </Modal>
        <Modal v-model="deleteFlag" title="删除算法" @on-ok="confirmDelete">
            <p>删除后不可恢复，确认要删除这个算法内容？</p>
        </Modal>
        <Modal v-model="newFlag" title="新建分组">
            <Input v-model="newName" placeholder="请输入新建的分组名称"/>
            <div slot="footer">
            <Button @click.native="newFlag = false;newName = ''">取消</Button>
            <Button type="primary" @click.native="confirmNewGroup">确认</Button>
            </div>
        </Modal>
    </div>
</template>

<script>
import backData from '@/assets/data.js'
export default {
data() {
    return {
        newName: "",
        newFlag: false,
        deleteGroupFlag: false,
        deleteFlag: false,
        deleteIndex: null,
        groupList: [],
        groupMap: {},
    };
},
computed: {
    isManager() {
        return common.isManager
    }
},
mounted() {
    this.getGroup()
},
methods: {
    // 获取分组
    getGroup() {
        common.get('classes/Group').then(res => {
            let arr = []
            res.results.forEach(ele => {
                arr.push(ele.objectId)
                ele.itemList = []
                this.groupMap[ele.objectId] = ele
            })
            this.getItemList(arr)
        })
    },
    // 获取ItemList
    getItemList(list) {
        let where = {
            groupId :{
                $in: list
            }
        }
        let info = '?where=' + JSON.stringify(where)
        info += '&keys=groupId,info,title,objectId'
        common.get('classes/itemList' + info).then(res => {
            res.results.forEach(ele => {
                this.groupMap[ele.groupId].itemList.push(ele)
            })
            for(let i in this.groupMap) {
                let item = this.groupMap[i]
                this.groupList.push(item)
            }
        })
    },
    cloneJson(obj) {
        return JSON.parse(JSON.stringify(obj));
    },
    // 确认新建分组
    confirmNewGroup() {
        if (!this.newName) {
            this.$Message.info("请输入分组的名称!");
            return;
        }
        this.newFlag = false;
        let name = this.cloneJson(this.newName);
        let data = {
            title: name,
        }
        common.post('classes/Group', data).then(res => {
            this.newName = "";
            this.groupList.push({
                objectId: res.objectId,
                title: name,
                itemList: []
            });
            this.$Message.success(name + "分组创建成功!");
        })
    },
    // 复制
    copy(index, eleIndex) {
        let item = this.cloneJson(this.groupList[index].itemList[eleIndex]);
        this.groupList[index].itemList.splice(eleIndex, 0, item);
        this.$Message.success("复制成功!");
    },
    deleteItem(index, eleIndex) {
        this.deleteIndex = {
        index: index,
        eleIndex: eleIndex
        };
        this.deleteFlag = true;
    },
    // 移动分组
    remove(index, eleIndex, groupIndex) {
        let item = this.groupList[index].itemList.splice(eleIndex, 1);
        this.groupList[groupIndex].itemList.push(item[0]);
        this.$Message.success("移动成功!");
    },
    // 删除系列
    confirmDelete() {
        let index = this.deleteIndex.index;
        let eleIndex = this.deleteIndex.eleIndex;
        let itemList = this.groupList[index].itemList
        common.delete('classes/itemList/' + itemList[eleIndex].objectId).then(res => {
            this.$Message.success('删除' + itemList[eleIndex].title +'成功!');
            itemList.splice(eleIndex, 1);
        })
    },
    deleteGroup(index) {
        this.deleteIndex = index;
        this.deleteGroupFlag = true;
    },
    // 删除分组
    confirmDeleteGroup() {
        let item = this.groupList[this.deleteIndex]
        let objectId = this.groupList[this.deleteIndex].objectId
        let data = [{
            method: 'DELETE',
            path: '/1/classes/Group/' + objectId
        }]
        item.itemList.forEach(ele => {
            data.push({
                method: 'DELETE',
                path: '/1/classes/itemList/' + ele.objectId
            })
        })
        common.post('batch', {
            requests: data
        }).then(res => {
            this.groupList.splice(this.deleteIndex, 1);
            this.$Message.success("删除分组成功!");
        })
    },
    // 编辑项目
    editItem(index, eleIndex) {
        let item = this.groupList[index].itemList[eleIndex]
        this.$router.push({
            path: '/edit',
            query: {
                id: item.objectId
            }
        })
    },
    seeInfo() {

    },
    newItem() {
        this.$router.push({
            path: '/new'
        })
    }
}
};
</script>

